header {
    z-index: 1;
    display: flex;
    justify-content: center;
    background-color: var(--secondary-color);
    height: clamp(80px, 14vh, 120px);
    width: 100%;
    box-shadow: 0px 3px 10px var(--shadow-light);
}

header .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: var(--max-width)
}

header a {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

header svg {
    color: black;
    filter: drop-shadow(1px 1px 1px var(--shadow-dark));

}

header h1 {
    margin: 0;
    padding: 0;
    font-size: 4.5rem;
    font-weight: 600;
    color: var(--text-main-color);
    text-shadow: 1px 1px 2px var(--shadow-dark);
}

#hero-logo,
#github-hero,
#user-avatar {
    height: 4.5rem;
}

#nav-user {
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 1.6rem;
    font-weight: 600;
}

#nav-user>p {
    cursor: pointer;
}

@media screen and (max-width: 600px) {


    header h1 {
        display: none;
    }
}